/* purgecss start ignore */
details {
  border-radius: 8px;
  display: block;

  summary {
    @include fs-title-5;
    background: $c-white;
    border: 3px solid $c-bright-blue;
    border-radius: 8px;
    color: $c-bright-blue;
    display: block;
    padding-right: 60px;

    &:hover {
      cursor: pointer;
    }

    &:after {
      @include background-image('plus-blue.svg');
      content: '';
      height: 30px;
      top: 18px;
      width: 30px;
    }
  }

  &[open] {
    summary {
      @include antialiasing;
      background: $c-bright-blue;
      border-radius: 8px 8px 0 0;
      color: $c-white;

      &:after {
        @include background-image('minus-white.svg');
        content: '';
      }
    }

    .details__content {
      border: 3px solid $c-bright-blue;
      border-radius: 0 0 8px 8px;
    }
  }
}

// Node specific styling overrides
.page-node-type-topic,
.page-node-type-department {

  .sfgov-resources details,
  .sfgov-services details {
    background: $c-white;

    summary {
      padding-right: 20px;
    }

    summary h3,
    summary .__title,
    summary h5 {
      @include fs-title-5;
      margin: 0;
      color: $c-bright-blue;
      font-size: 20px;
      font-weight: $fw-medium;
      padding-right: 45px;

      &:hover {
        cursor: pointer;
      }

      &:after {
        @include background-image('plus-blue.svg');
        content: '';
        height: 30px;
        top: 18px;
        width: 30px;
      }

      .sfgov-container-item {
        margin-bottom: 0;
      }
    }

    .sfgov-container-item {
      margin-bottom: 40px;

      @include media-max($mobile-screen) {
        margin-bottom: 40px;

        .sfgov-resource-card {
          background-color: white;
          border: 0;
          border-radius: 0;
          padding: 0px;
          background-color: transparent;
        }
      }
    }

    .sfgov-service-card {
      padding: 0;
      border: none;
    }

    &[open] {

      summary h3,
      summary .__title,
      summary h5 {
        color: $c-white;
      }
    }
  }

  @include media-max($mobile-screen) {
    .sfgov-resources details {
      margin: 0 0 20px !important;

      .sfgov-container-item {
        margin-bottom: 40px !important;

        .sfgov-container-item {
          margin-bottom: 0 !important;
        }
      }
    }

    .sfgov-services .sfgov-services-section {
      margin-bottom: 20px;
    }
  }
}

/* purgecss end ignore */