/* purgecss start ignore */
.page-node-type-form-confirmation-page {
  // Content.
  .confirmation--content {
    @include contain-1090;
    @include clearfix;
    margin-top: 60px;
    position: relative;
  }

}

// Content left.
.confirmation--left {
  @include columns-780(left);

  .paragraph--type--step {
    margin: 0 0 60px;

    h4 {
      @include fs-title-5;
    }

    .__content {
      .field__item {
        margin-bottom: 30px;
      }
    }
  }

  .field--type-entity-reference-revisions .field__label {
    display: none;
  }
}

// Content right.
.confirmation--right {
  @include columns-350(right);
}

.confirmation--right-content {
  background: $c-blue-1;
  border-radius: 8px;
  margin-bottom: 20px;

  h2 {
    @include fs-title-3-mobile;
    margin-bottom: 34px;

    @include media($medium-screen) {
      @include fs-title-3;
    }
  }

  .confirmation--right-para {
    margin-bottom: 40px;
    padding: 40px;
  }

  .paragraph--type--in-person-location,
  .paragraph--type--email,
  .paragraph--type--phone {
    background: none;
    padding: 0;
  }
  .__confirmation-sidebar {
    padding: 40px;
    .field__label {
      @include fs-title-3;
      margin-bottom: 30px;
    }
    .field__items {
      > .field__item {
        margin-bottom: 40px;
        &:last-child {
          margin-bottom: 0;
        }
      }
      .field__item {
        .field__label, .__title {
          @include fs-title-5;
          margin-bottom: 10px;
        }
      }
    }
    .sfgov-phone h4, .sfgov-email h4 {
      @include fs-body-short;
      margin-bottom: 10px;
    }
  }

  .sfgov-email {
    .sfgov-email-title {
      @include fs-title-5;
    }
  }
}
/* purgecss end ignore */
