/* purgecss start ignore */
details {
  margin: 15px 0;
  summary {
    background: $c-grey-3;
    font-weight: $fw-medium;
    padding: 20px;
    position: relative;
    &::-webkit-details-marker{
      display: none;
    }
  
    &:after{
      position: absolute;
      top: 11px;
      right: 27px;
      content: "+";
      font-size: 2em;
  
      details[open] & {
        content: "-";
      }
    }
  }
}

.details__content {
  padding: 20px;
}
/* purgecss end ignore */
